import React, { Component } from 'react';
import {
  App,
  View,
  Statusbar,
} from 'framework7-react';
import routes from '../js/routes';

export default class extends Component {
  constructor() {
    super();

    this.state = {
      // Framework7 Parameters
      f7params: {
        name: 'DearMeal', // App name
        theme: 'auto', // Automatic theme detection
        // App root data
        data: function () {
          return {};
        },

        // App routes
        routes: routes,

        // // Register service worker
        // serviceWorker: {
        //   path: '/service-worker.js',
        // },
      },
      pushStateRoot: process.env.HOST
    }
  }
  render() {
    return (
      <App params={ this.state.f7params }>
        {/* Status bar overlay for fullscreen mode*/}
        <Statusbar></Statusbar>

        {/* Your main view, should have "view-main" class */}
        <View main className="safe-areas" url="/" pushState pushStateRoot={this.state.pushStateRoot} stackPages />
      </App>
    )
  }
  componentDidMount() {
    this.$f7ready((f7) => {
      // Call F7 APIs here
    });
  }
}