import React from 'react';
import {
  Page,
  Navbar,
  NavTitle,
  NavTitleLarge,
  Block,
  Col,
  Preloader,
} from 'framework7-react';
import http from '../js/http';
import MenuDetail from '../components/MenuDetail.jsx';

export default class extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      detail: {},
    };
  }

  async componentDidMount() {
    const result = await http.get('/api/v1/dearmeal/list');

    if (result && result.success) {
      this.setState({
        data: result.data
      });
    }
  }

  render() {
    const isEmpty = this.state.data.length === 0;

    return (
      <Page name="home">
        <Navbar sliding={false} large>
          <NavTitle sliding>DearMeal</NavTitle>
          <NavTitleLarge>DearMeal</NavTitleLarge>
        </Navbar>

        {
          !isEmpty ?
          (
              this.state.data.map((item) => {
                return (
                  <MenuDetail {...item} key={item.id}></MenuDetail>
                );
              })
          ) : 
          (
              <Block className="row text-align-center">
                <Col>
                  <Preloader color="pink"></Preloader>
                </Col>
              </Block>
          )
        }
      </Page>
    );
  }
}
