import React from 'react';
import {
  Page,
  Navbar,
  NavRight,
  Searchbar,
  BlockTitle,
  Card,
  CardHeader,
  CardContent,
  CardFooter,
  Link,
  SkeletonBlock,
} from 'framework7-react';
import Clipboard from 'react-clipboard.js';
import http from '../js/http';

export default class extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      raw: [],
      list: [],
      ver: '',
    };
  }

  async componentDidMount() {
    const result = await http.get('https://ibdkopi6vn.erjsnfs.com/api/v1/alipayscheme/list');

    if (result && result.success) {
      const raw = result.data.list;
      this.setState({
        raw,
        list: [...raw],
        ver: result.data.ver,
      });
    }
  }

  onCopySuccess = () => {
    if (!this.toast) {
      this.toast = this.$f7.toast.create({
        text: '已复制',
        closeTimeout: 2000,
      });
    }

    this.toast.open();
  }

  onPageBeforeOut = () => {
    this.$f7.toast.close();
  }

  onPageBeforeRemove = () => {
    if (this.toast) this.toast.destroy();
  }

  onSearch = (event) => {
    this.filterData(event.target.value)
  }

  filterData = (str) => {
    if (str === '') {
      this.setState({
        list: [...this.state.raw]
      });
    } else {
      const list = this.state.raw.filter((item) => {
        return item.name.includes(str) || item.desc.includes(str) || item.appId.includes(str) || item.schemaUri.includes(str);
      });
      this.setState({
        list,
      });
    }
  }

  render() {
    const isEmpty = this.state.list.length === 0;

    return (
      <Page name="schemes" onPageBeforeRemove={this.onPageBeforeRemove} onPageBeforeOut={this.onPageBeforeOut}>
        <Navbar title="Alipay Schemes">
          <NavRight>
            <Link searchbarEnable iconIos="f7:search_strong" iconMd="material:search"></Link>
          </NavRight>
          <Searchbar onSearchbarDisable={() => this.filterData('')} onSearchbarClear={() => this.filterData('')} onChange={this.onSearch} expandable customSearch={true} placeholder={'搜索'} disableButtonText={'取消'}></Searchbar>
        </Navbar>

        {
          !isEmpty ?
          (
            <>
              <BlockTitle>版本: {this.state.ver} / 共{this.state.raw.length}条</BlockTitle>
              {
                this.state.list.map((item) => {
                  return (
                    <Card key={item.appId}>
                      <CardHeader>{item.name}</CardHeader>
                      <CardContent>
                        <div style={{wordBreak: 'break-all'}}>
                          {item.schemaUri}
                        </div>
                      </CardContent>
                      <CardFooter>
                        <Clipboard component="a" data-clipboard-text={item.schemaUri} onSuccess={this.onCopySuccess}>
                          复制
                        </Clipboard>
                        <Link href={item.schemaUri} external target={"_blank"}>前往</Link>
                      </CardFooter>
                    </Card>
                  );
                })
              }
            </>
          ) : 
          (
            <>
              <BlockTitle className="skeleton-text skeleton-effect-fade"><SkeletonBlock style={{ width: '135px', height: '19px' }} /></BlockTitle>
              {
                [1, 2, 3].map((num) => {
                  return (
                    <Card
                      key={num}
                      className="skeleton-text skeleton-effect-fade"
                    >
                      <CardHeader>
                        <SkeletonBlock style={{ width: '56px', height: '19px' }} />
                      </CardHeader>
                      <CardContent>
                        <SkeletonBlock style={{ width: '325px', height: '19px' }} />
                      </CardContent>
                      <CardFooter>
                        <SkeletonBlock style={{ width: '28px', height: '19px' }} />
                        <SkeletonBlock style={{ width: '28px', height: '19px' }} />
                      </CardFooter>
                    </Card>
                  )
                })
              }
            </>
          )
        }
      </Page>
    );
  }
}
